.contact{
   padding-top: 200px;
   
   >h1{
      color: white;
      text-align: center;
      font-size: 60px;
      text-transform: uppercase;
      text-shadow: 2px;
      font-weight: bolder;
   }
}

	

form {
    padding-top: 0px;
    max-width: 600px;
    text-align: center;
    margin: 20px auto;
   
   input, textarea {
      border:0; outline:0;
      padding: 1em;
      border-radius:8px;
      display: block;
      width: 100%;
      margin-top: 1em;
      font-family: 'Merriweather', sans-serif;
      box-shadow:0 1px 1px rgba(black, 0.1);
      resize: none;
     
     &:focus {
        box-shadow:0 0px 2px rgba(red, 1);
     }
   }
   
   #input-submit {
      color: white; 
      background: red;
      cursor: pointer;
     
     &:hover {
        box-shadow:0 1px 1px 1px rgba(#aaa, 0.6); 
     }
   }
   
   textarea {
       height: 126px;
   }
 }
 
 
 .half {
   float: left;
   width: 48%;
   margin-bottom: 1em;
 }
 
 .right { width: 50%; }
 
 .left {
      margin-right: 2%; 
 }
 
 
 @media (max-width: 480px) {
   .half {
      width: 100%; 
      float: none;
      margin-bottom: 0; 
   }
 }
 
 
 /* Clearfix */
 .cf:before,
 .cf:after {
     content: " "; /* 1 */
     display: table; /* 2 */
 }
 
 .cf:after {
     clear: both;
 }
 